.table {
  padding: 5px;
  width: 100%;
  overflow-x: auto;

  table {
    border-collapse: collapse;
    width: 100%;
    font-size: 1.4rem;

    thead {
      border-top: 2px solid var(--light-blue);
      border-bottom: 2px solid var(--light-blue);
    }

    th {
      border: 1px solid #eee;
    }

    th,
    td {
      vertical-align: top;
      text-align: center;
      align-items: center;
      padding: 8px;

      .count {
        display: flex;
        justify-content: center;
      }

      &.icons {
        > * {
          margin-right: 5px;
          cursor: pointer;
        }
      }
    }


    tr {
      border-bottom: 1px solid #ccc;
    }

    tr:nth-child(even) {
      background-color: #eee;
    }
  }

  .summary {
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: start;

    & > div {
      display: flex;
    }

    .card {
      padding: 1rem;

      .text {
        display: flex;
        justify-content: space-between;
        align-items: center;

        h3 {
          color: var(--color-danger);
        }
      }

      button {
        margin-top: 5px;
      }
    }
  }
}

.count {
  display: flex;
  align-items: center;

  button {
    border: 1px solid var(--darkblue);
  }

  p {
    margin-left: 4px;
  }
;

  & > * {
    margin-right: 1rem;
  }
}

